import React, { useState, useRef, useEffect } from 'react';
import * as faceapi from 'face-api.js';
import { sendImageForLiveness } from '../utils/api';
import '../components/LivenessPopup.css'; 
import dikriptLogo from '../assets/dikript-logo.png'; 

const LivenessPopup = ({ apiKey, name, apiUrl, onClose, onLivenessResult }) => {
  const [currentStep, setCurrentStep] = useState(1);
  const [message, setMessage] = useState('');
  const [isError, setIsError] = useState(false);
  const [faceDetected, setFaceDetected] = useState(false);
  const [consentGiven, setConsentGiven] = useState(false);
  const [showCamera, setShowCamera] = useState(false);
  const [processing, setProcessing] = useState(false);
  const [stream, setStream] = useState(null);
  const [showRetry, setShowRetry] = useState(false);
  const [isSuccess, setIsSuccess] = useState(false);
  const [livenessCheckCompleted, setLivenessCheckCompleted] = useState(false);
  const [isCapturing, setIsCapturing] = useState(false);

  const videoRef = useRef(null);
  const overlayRef = useRef(null);
  const detectionIntervalRef = useRef(null);

  useEffect(() => {
    return () => {
      stopCamera();
    };
  }, []);

  const proceedAfterConsent = () => {
    if (consentGiven) {
      setCurrentStep(2);
    }
  };

  const startCamera = async () => {
    setShowCamera(true);
    try {
      await loadFaceDetectionModels();
      const mediaStream = await navigator.mediaDevices.getUserMedia({ video: true });
      setStream(mediaStream);
      if (videoRef.current) {
        videoRef.current.srcObject = mediaStream;
      }
      setMessage('Position your face in the frame');
      startFaceDetection();
    } catch (error) {
      console.error('Error accessing camera:', error);
      showError('Unable to access the camera. Please ensure you have given permission.');
    }
  };

  const loadFaceDetectionModels = async () => {
    setMessage('Loading face detection models...');
    await faceapi.nets.tinyFaceDetector.loadFromUri('/models');
    await faceapi.nets.faceLandmark68Net.loadFromUri('/models');
  };

  const startFaceDetection = () => {
    detectionIntervalRef.current = setInterval(async () => {
      if (videoRef.current && overlayRef.current) {
        const detections = await faceapi.detectAllFaces(videoRef.current, new faceapi.TinyFaceDetectorOptions())
          .withFaceLandmarks();
        
        const overlay = overlayRef.current;
        overlay.width = videoRef.current.videoWidth;
        overlay.height = videoRef.current.videoHeight;
        
        const ctx = overlay.getContext('2d');
        ctx.clearRect(0, 0, overlay.width, overlay.height);
        
        if (detections.length > 0) {
          faceapi.draw.drawDetections(overlay, detections);
          faceapi.draw.drawFaceLandmarks(overlay, detections);
          setFaceDetected(true);
          setMessage('Face detected. Click "Capture Photo" when ready.');
        } else {
          setFaceDetected(false);
          setMessage('No face detected. Please position your face in the frame.');
        }
      }
    }, 100);
  };

  const capturePhoto = async () => {
    if (!faceDetected) {
      showError('No face detected. Please position your face in the frame.');
      return;
    }
    
    setIsCapturing(true);
    setMessage('Processing...');
    
    clearInterval(detectionIntervalRef.current);
    
    const video = videoRef.current;
    const canvas = document.createElement('canvas');
    canvas.width = video.videoWidth;
    canvas.height = video.videoHeight;
    canvas.getContext('2d').drawImage(video, 0, 0, canvas.width, canvas.height);
    
    const dataUrl = canvas.toDataURL('image/png');
    await processImage(dataUrl);
  };

  const processImage = async (dataUrl) => {
    try {
      setProcessing(true);
      const response = await sendImageForLiveness(dataUrl, apiKey, apiUrl);
      if (response.status) {
        onLivenessResult(response);
        setMessage('Liveness check completed successfully.');
        setIsSuccess(true);
        setShowCamera(false);
        setLivenessCheckCompleted(true);
      } else {
        throw new Error(response.message || 'Liveness check failed.');
      }
    } catch (error) {
      console.error('Error processing image:', error);
      showError(error.message || 'Failed to process the image. Please try again.');
      setShowRetry(true);
    } finally {
      setProcessing(false);
      setIsCapturing(false);
    }
  };

  const retryCapture = () => {
    setShowRetry(false);
    setShowCamera(true);
    setFaceDetected(false);
    setMessage('');
    startCamera();
  };

  const showError = (message) => {
    setMessage(message);
    setIsError(true);
  };

  const stopCamera = () => {
    if (stream) {
      stream.getTracks().forEach(track => track.stop());
    }
    clearInterval(detectionIntervalRef.current);
    setShowCamera(false);
  };

  const closePopup = () => {
    stopCamera();
    setLivenessCheckCompleted(false);
    onClose();
  };

  return (
    <div className="liveness-popup">
      <div className="popup-content">
        <div className="header">
          {currentStep > 1 && <button className="back-button" onClick={() => setCurrentStep(prev => prev - 1)}></button>}
          <img src={dikriptLogo} alt="Dikript logo" className="logo" />
          <button className="close-button" onClick={closePopup}></button>
        </div>
        <h2>Liveness Check</h2>
        {currentStep === 1 ? (
          <div>
            <p className="subtitle">{name} will access the following information:</p>
            <div className="permissions">
              <div className="permission-group">
                <h3>{name} will be able to</h3>
                <ul>
                  <li>Access your camera</li>
                  <li>Capture your photo</li>
                  <li>Perform liveness check</li>
                </ul>
              </div>
              <div className="permission-group">
                <h3>{name} will not be able to</h3>
                <ul>
                  <li>Store your photo permanently</li>
                  <li>Access other device features</li>
                  <li>Share your information with third parties</li>
                </ul>
              </div>
            </div>
            <div className="consent">
              <label>
                <input
                  type="checkbox"
                  checked={consentGiven}
                  onChange={() => setConsentGiven(!consentGiven)}
                />
                I consent to share this information with {name}
              </label>
            </div>
            <button onClick={proceedAfterConsent} disabled={!consentGiven} className="action-button">
              Continue
            </button>
          </div>
        ) : currentStep === 2 && !showCamera && !livenessCheckCompleted ? (
          <div className="center-content">
            <p className="consent-confirmation">Proceed to liveness check</p>
            <button onClick={startCamera} className="action-button">
              Start Camera
            </button>
          </div>
        ) : showCamera ? (
          <div>
            <div className="camera-container">
              <video ref={videoRef} autoPlay muted playsInline></video>
              <canvas ref={overlayRef} className="face-overlay"></canvas>
            </div>
            <p className={`message ${isError ? 'error' : ''} ${isSuccess ? 'success' : ''}`}>
              {isCapturing ? 'Processing...' : message}
            </p>
            <div className="button-container">
              {faceDetected && !processing && !isCapturing && (
                <button onClick={capturePhoto} className="capture-button">
                  Capture Photo
                </button>
              )}
              {showRetry && (
                <button onClick={retryCapture} className="retry-button">
                  Retry
                </button>
              )}
            </div>
          </div>
        ) : livenessCheckCompleted ? (
          <div className="center-content">
            <p className="success-message">Liveness check completed successfully.</p>
            <button onClick={closePopup} className="action-button">
              Close
            </button>
          </div>
        ) : null}
        <div className="footer">
          Powered by Dikript
        </div>
      </div>
    </div>
  );
};

export default LivenessPopup;
